<template>
	<div class="customHeader">
			<yd-navbar slot="navbar" title="内部邮件">
				<a @click="goToIndex" slot="left">
					<yd-navbar-back-icon></yd-navbar-back-icon>
				</a>
				<a slot="right" @click="sendMailFunc">
					<span>新建</span>
				</a>
			</yd-navbar>
		<section>
			<move :listData="dateList">
				<div slot="page1">
					<mail-list listData="recieveFolderId"></mail-list>
				</div>
				<div slot="page2">
					<mail-list listData="sendMailFolderId"></mail-list>
				</div>
				<div slot="page3">
					<mail-temporary listData="DraftMail"></mail-temporary>
				</div>
				<div slot="page4">
					<mail-temporary listData="RecyclMail"></mail-temporary>
				</div>
			</move>
		</section>
	</div>
	
</template>


<script type="text/ecmascript-6">
	import Move from '@/components/Move';
	import MailList from '@/components/oa/MailList';
	import MailTemporary from '@/components/oa/MailTemporary';
	export default {
		data(){
			return{
				dateList:{
					icon:{
						1:"recieve",
						2:"sendMail",
						3:"draft",
						4:"recycl"
					},
					title:{
						1:"收件箱",
						2:"发件箱",
						3:"草稿箱",
						4:"垃圾箱"
					}
				}
			}
		},
		methods: {
			sendMailFunc() {
				this.$router.push({
					name: 'sendMail',
					params: '{mailType:mailType}'
				});
			}
		},
		created() {	
			
		},
		components: {
			Move,
			MailList,
			MailTemporary
		}
	}
</script>

<style scoped>
	
</style>


