<template>
	<div>
		<div v-if="mailList.length==0" class="nodata">
			<yd-icon slot="icon" name="nodata"></yd-icon>
			<span class="noDataTips">无记录</span>
		</div>
		<yd-list theme="4" v-else>
			<div>
				<rx-list-item 
					v-for="mail in mailList" 
					:key="mail.pkId" 
					:href="{name:'innerMailDetail',params:{mail:mail,mailType:'draft',oldFolderId:mailFolderData.draftFolderId}}" 
					type="link"
				>
					<div class="mail-img">
						<span>{{getFirstLetter(mail.recNames)}}</span>
					</div>
					<div class="list-other">
						<div class="draft-content">
							<span>{{mail.recNames}}</span>
							<span>{{mail.createTime}}</span>
						</div>
						<h1>{{mail.subject}}</h1>
					</div>
				</rx-list-item>
			</div>
		</yd-list>
	</div>

</template>


<script type="text/ecmascript-6">
	import Vue from 'vue';
	import listItem from '@/components/common/ListItem';
	import { loadRefreshList } from '@/assets/app.js';
	import { Button, ButtonGroup } from 'vue-ydui/dist/lib.rem/button';
	import router from '@/router/index';
	import { CheckBox, CheckBoxGroup } from 'vue-ydui/dist/lib.rem/checkbox';
	Vue.component(CheckBox.name, CheckBox);
	Vue.component(CheckBoxGroup.name, CheckBoxGroup);
	Vue.component(Button.name, Button);
	Vue.component(ButtonGroup.name, ButtonGroup);
	Vue.component(listItem.name, listItem);

	export default {
		data() {
			return {
				mailList: [],
				userFolderArray: [],
				mailFolderData: {
					recieveFolderId: "",
					sendMailFolderId: "",
					draftFolderId: "",
					recyclFolderId: "",
				}
			}
		},
		props:{
			listData:String
		},
		methods: {
			getDraftMailList() {
				var url = _baseUrl + "/mobile/oa/get"+this.listData+"List.do";
				this.$ajax({
					url: url,
					method: 'POST',
				}).then(response => {
					this.mailList = response.data.data;
				}).catch(error => {
					console.log(error);
				});
			},
			getUserMailFolder() {
				let storeFolderData = this.$store.state.mailModule.folderData;
				//缓存已有数据			
				if(storeFolderData[0] != null) {
					this.mailFolderData = storeFolderData;
					return;
				}
				var url = _baseUrl + "/mobile/oa/getUserMailFolder.do";
				this.$ajax({
					url: url,
					method: 'POST',
				}).then(response => {
					this.userFolderArray = response.data.data;
					//处理用户文件夹数据
					this.handUserFolder(this.userFolderArray);
					//把数据存入vuex进行状态管理
					this.$store.commit('setFolderData', this.mailFolderData);
					this.getDraftMailList();
				}).catch(error => {
					console.log(error);
				});
			},
			handUserFolder(userFolder) {
				if(userFolder == null) {
					return;
				}
				for(let i = 0; i < userFolder.length; i++) {
					switch(userFolder[i].type) {
						case 'RECEIVE-FOLDER':
							this.mailFolderData.recieveFolderId = userFolder[i].folderId;
							break;
						case 'SENDER-FOLDER':
							this.mailFolderData.sendMailFolderId = userFolder[i].folderId;
							break;
						case 'DRAFT-FOLDER':
							this.mailFolderData.draftFolderId = userFolder[i].folderId;
							break;
						case 'DEL-FOLDER':
							this.mailFolderData.recyclFolderId = userFolder[i].folderId;
							break;
						default:
							break;
					}
				}
			},
			getFirstLetter(word){
				return word.substr(0,1);
			}
		},
		created(){
			this.getUserMailFolder();
		},
		components: {
			
		}

	}
</script>

<style scoped>
	
	.yd-list-theme4{
		/*background: transparent;*/
		border-radius: .12rem;
		/*overflow: hidden;*/
	}
	
	.yd-icon-nodata:before {
		font-size: 2rem;
		color: #A9A9A9
	}
	
	.nodata {
		width: 50%;
		margin: 2rem auto 0 auto;
	}
	
	.nodata i {
		text-align: center;
		display: block;
	}
	
	.noDataTips {
		font-size: 0.3rem;
		text-align: center;
		width: 100%;
		display: block;
	}
	
	.list-other {
		font-size: 0.3rem;
		color: #666;
		font-weight: normal;
	}
	
	.yd-icon-edit:before {
		font-size: 0.3rem
	}
	.demo-content {
  		padding: 10px 10px;
  	}
  	.mail{
  		font-size: 0.2rem;
  	}
  	.mail div{
  		/*margin: 0 auto 0.1rem auto;*/
  	}
  	
  	.mail-header{
  		display: flex;
  		width: 100%;
  		align-items: center;
  	}
  	
  	.mail-recNames{
  		font-size: 0.3rem;
  		width: calc(100% - 2.4rem);
  		display: block;
  		white-space:nowrap;
  		/*text-overflow:ellipsis;*/
  		overflow-x: auto;
  	}
  	
  	.mail-recNames1{
  		width: calc(100% - 2.58rem);
  	}
  	
  	.mail-createTime{
  		display: block;
  		text-align: right;
  		color: #B2B2B2;
  		font-size: .22rem;
  		width: 2.4rem;
  	}
  	
  	.mail-subject{
  		display: block;
  		width: 100%;
  		font-size: 0.26rem;
  	}
  	.mail-content{
  		color: #B2B2B2;
  		font-size: 0.26rem;
  	}
  	
  	.mail-recNames,.mail-subject{
  		color: #666;
  	}
  	
  	.vux-1px-b{
  		border-bottom: 1px solid #ececec;
  		padding: 0.12rem 0 !important;
  		margin: 0 .12rem;
  		display: flex;
  		/*justify-content: center;*/
  		align-items: center;
	}
	
	.vux-1px-b::after{
		content: '';
		display: block;
		clear: both;
	}
	
	
	.vux-swipeout-item:last-child .vux-1px-b{
		border-bottom: none;
	}
	
	.mail-img,.mail-img img,.mail-img span{
		width: .8rem;
		height: .8rem;
	}

	.mail-img{
		border-radius: 1rem;
		overflow: hidden;
		margin-right: .12rem;
		float: left;
	}
	
	.mail-img span{
		display: block;
		box-sizing: border-box;
		border: 1px solid #ececec;
		border-radius: 1rem;
		line-height: .8rem;
		text-align: center;
		font-size: .4rem;
		color: #999;
	}
	

	.mail{
		float: left;
		width: calc(100% - .92rem);
	}
	
	.vux-1px-tb{
		border-radius: .12rem;
	}
  	
  	.mail-content{
  		overflow:hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
		text-align: justify;
  	}
  	
  	.yd-list-theme4 >>> .yd-list-mes{
  		padding: 0;
  		display: flex;
  		width: 100%;
  	}
  	
  	.yd-list-theme4 >>> .yd-list-mes .list-other{
  		width: calc(100% - .92rem);
  	}
	
	.yd-list-theme4 >>> .yd-list-mes .list-other h1{
	    overflow: hidden;
	    text-overflow: ellipsis;
	    display: -webkit-box;
	    -webkit-line-clamp: 2;
	    -webkit-box-orient: vertical;
	    text-align: justify;
        color: #B2B2B2;
    	font-size: 0.26rem;
  	}
	
	
	
	.yd-list-theme4 >>> .draft-content{
		display: flex;
		justify-content: space-between;
	}
	
	.yd-list-theme4 >>> .draft-content span{
  		white-space:nowrap;
  		/*text-overflow:ellipsis;*/
  		overflow-x: auto;
  		display: block;
	}
	.yd-list-theme4 >>> .draft-content span:nth-of-type(1){
		/*width: clac( 100% - 2.4rem );*/
		width: calc(100% - 2.4rem);
	}
	
	.yd-list-theme4 >>> .draft-content span:nth-of-type(2){
	    text-align: right;
	    color: #B2B2B2;
	    font-size: .22rem;
	    width: 2.4rem;
	}
	
</style>


