<template>
	<div>
		<div v-if="mailList.length==0" class="nodata">
			<yd-icon slot="icon" name="nodata"></yd-icon>
			<span class="noDataTips">无记录</span>
		</div>
		<yd-list theme="4" v-else>
			<div @touchstart.stop @touchmove.stop @touchend.stop>
				<swipeout class="vux-1px-tb">
					<swipeout-item 
						transition-mode="follow" 
						v-for="(mail,index) in mailList" 
						:key="index" 
					>
						<div slot="right-menu">
							<swipeout-button type="warn" @touchstart.native="deleteMail(mail,'recieve')">删除</swipeout-button>
						</div>
						<div 
							slot="content" 
							:class="{'vux-1px-b': index !== mailList.length, 'vux-1px-t': index === 1}" 
							@click="getMailDetail(mail,'recieve')"
						>
							<div class="mail-img">
								
								<span>{{getFirstLetter(mail.sender)}}</span>
							</div>
							<div class="mail">
								<div class="mail-header">
									<span class="mail-unread" v-show="mail.isRead !== 'YES'"></span>
									<span 
										class="mail-recNames" 
										:class="{'mail-recNames1': mail.isRead !== 'YES'}"
									>{{mail.sender}}</span>
															
									<span class="mail-createTime">{{mail.createTime}}</span>
								</div>
								<div>
									<span class="mail-subject">{{mail.subject}}</span>									
								</div>
								<div>
									<span class="mail-content" v-html="getAbstract(mail.content)"></span>									
								</div>
							</div>								
						</div>
					</swipeout-item>
				</swipeout>
			</div>
		</yd-list>
	</div>

</template>


<script type="text/ecmascript-6">
	import Vue from 'vue';
	import listItem from '@/components/common/ListItem';
	import { loadRefreshList } from '@/assets/app.js';
	import { Button, ButtonGroup } from 'vue-ydui/dist/lib.rem/button';
	import router from '@/router/index';
	import { CheckBox, CheckBoxGroup } from 'vue-ydui/dist/lib.rem/checkbox';
	import { GroupTitle, Swipeout, SwipeoutItem, SwipeoutButton, XButton } from 'vux';
	Vue.component(CheckBox.name, CheckBox);
	Vue.component(CheckBoxGroup.name, CheckBoxGroup);
	Vue.component(Button.name, Button);
	Vue.component(ButtonGroup.name, ButtonGroup);
	Vue.component(listItem.name, listItem);

	export default {
		data() {
			return {
				mailList: [],
				userFolderArray: [],
				mailFolderData: {
					recieveFolderId: "",
					sendMailFolderId: "",
					draftFolderId: "",
					recyclFolderId: "",
				}
			}
		},
		props:{
			listData:String
		},
		methods: {
			getRecieveMailList() {
				var url = _baseUrl + "/mobile/oa/getInnerMailByFolderId.do";
				this.$ajax({
					url: url,
					method: 'POST',
					params:{
						folderId:this.mailFolderData[this.listData]
					}
				}).then(response => {
					this.mailList = response.data.data;
				}).catch(error => {
					console.log(error);
				});
			},
			getUserMailFolder() {
				let storeFolderData = this.$store.state.mailModule.folderData;
				//缓存已有数据			
				if(storeFolderData[0] != null) {
					this.mailFolderData = storeFolderData;
					return;
				}
				var url = _baseUrl + "/mobile/oa/getUserMailFolder.do";
				this.$ajax({
					url: url,
					method: 'POST',
				}).then(response => {
					this.userFolderArray = response.data.data;
					//处理用户文件夹数据
					this.handUserFolder(this.userFolderArray);
					//把数据存入vuex进行状态管理
					this.$store.commit('setFolderData', this.mailFolderData);
					this.getRecieveMailList();
				}).catch(error => {
					console.log(error);
				});
			},
			handUserFolder(userFolder) {
				if(userFolder == null) {
					return;
				}
				for(let i = 0; i < userFolder.length; i++) {
					switch(userFolder[i].type) {
						case 'RECEIVE-FOLDER':
							this.mailFolderData.recieveFolderId = userFolder[i].folderId;
							break;
						case 'SENDER-FOLDER':
							this.mailFolderData.sendMailFolderId = userFolder[i].folderId;
							break;
						case 'DRAFT-FOLDER':
							this.mailFolderData.draftFolderId = userFolder[i].folderId;
							break;
						case 'DEL-FOLDER':
							this.mailFolderData.recyclFolderId = userFolder[i].folderId;
							break;
						default:
							break;
					}
				}
			},
			getAbstract(mailContent){
				if(mailContent.length==0||mailContent=="无"){
					return "（无摘要）";
				}else if(mailContent.length>=30){
					return mailContent.substr(0,30) + "...";
				}else{
					return mailContent;
				}				
			},
			deleteMail(mail,mailType){
				debugger;
				var url = _baseUrl + "/mobile/oa/moveInnerMailToFolder.do";
				this.$ajax({
					url: url,
					method: 'POST',
					params:{
						target:"del",
						ids:mail.mailId,
						nowFolderId:this.mailFolderData.sendMailFolderId				
					}
				}).then(response => {
					if(response.data.success){
						this.successToast("邮件丢进垃圾箱了",1000);
					}
					if(mailType=='recieve'){						
						this.getRecieveMailList();
					}else if(mailType=='sendMail'){
						this.getSendedMailList();
					}else if(mailType=='draft'){
						oldFolderId = this.mailFolderData.draftFolderId;
					}else{
						oldFolderId = this.mailFolderData.recyclFolderId;
					}
				}).catch(error => {
					console.log(error);
				});
			},
			getMailDetail(mail,mailType){
				let oldFolderId = "";
				if(mailType=='recieve'){
					oldFolderId = this.mailFolderData.recieveFolderId;
				}else if(mailType=='sendMail'){
					oldFolderId = this.mailFolderData.sendMailFolderId;
				}else if(mailType=='draft'){
					oldFolderId = this.mailFolderData.draftFolderId;
				}else{
					oldFolderId = this.mailFolderData.recyclFolderId;
				}
				router.push({name:'innerMailDetail',params:{mail:mail,mailType:mailType,oldFolderId:oldFolderId}});
			},
			getFirstLetter(word){
				return word.substr(0,1);
			}
		},
		created(){
			this.getUserMailFolder();
		},
		components: {
		    Swipeout,
		    SwipeoutItem,
		    SwipeoutButton,
		    XButton
		}

	}
</script>

<style scoped>
	
	.yd-list-theme4{
		border-radius: .12rem;
	}
	
	.yd-icon-nodata:before {
		font-size: 2rem;
		color: #A9A9A9
	}
	
	.nodata {
		width: 50%;
		margin: 2rem auto 0 auto;
	}
	
	.nodata i {
		text-align: center;
		display: block;
	}
	
	.noDataTips {
		font-size: 0.3rem;
		text-align: center;
		width: 100%;
		display: block;
	}
	
	.list-other {
		font-size: 0.3rem;
		color: #666;
		font-weight: normal;
	}
	
	.yd-icon-edit:before {
		font-size: 0.3rem
	}
	.demo-content {
  		padding: 10px 10px;
  	}
  	.mail{
  		font-size: 0.2rem;
  	}
  	
  	.mail-header{
  		display: flex;
  		width: 100%;
  		align-items: center;
  	}
  	
  	.mail-unread{
  		width: 8px;
  		height: 8px;
  		border-radius: 8px;
  		float: left;
  		background: #3399ff;
  		margin-right: .06rem;
  	}
  	
  	.mail-recNames{
  		font-size: 0.3rem;
  		width: calc(100% - 2.4rem);
  		display: block;
  		white-space:nowrap;
  		overflow-x: auto;
  	}
  	
  	.mail-recNames1{
  		width: calc(100% - 2.58rem);
  	}
  	
  	.mail-createTime{
  		display: block;
  		text-align: right;
  		color: #B2B2B2;
  		font-size: .22rem;
  		width: 2.4rem;
  	}
  	
  	.mail-subject{
  		display: block;
  		width: 100%;
  		font-size: 0.26rem;
  	}
  	.mail-content{
  		color: #B2B2B2;
  		font-size: 0.26rem;
  	}
  	
  	.mail-recNames,.mail-subject{
  		color: #666;
  	}
  	
  	.vux-1px-b{
  		border-bottom: 1px solid #ececec;
  		padding: 0.12rem 0 !important;
  		margin: 0 .12rem;
  		display: flex;
  		align-items: center;
	}
	
	.vux-1px-b::after{
		content: '';
		display: block;
		clear: both;
	}
	
	
	.vux-swipeout-item:last-child .vux-1px-b{
		border-bottom: none;
	}
	
	.mail-img,.mail-img img,.mail-img span{
		width: .8rem;
		height: .8rem;
	}

	.mail-img{
		border-radius: 1rem;
		overflow: hidden;
		margin-right: .12rem;
		float: left;
	}
	
	.mail-img span{
		display: block;
		box-sizing: border-box;
		border: 1px solid #ececec;
		border-radius: 1rem;
		line-height: .8rem;
		text-align: center;
		font-size: .4rem;
		color: #999;
	}
	

	.mail{
		float: left;
		width: calc(100% - .92rem);
	}
	
	.vux-1px-tb{
		border-radius: .12rem;
	}
	
</style>


